:root{
  --primary-color:rgba(0,4,22);
  --secondary-color:rgb(99, 192, 255);
  --tertiary-color:rgb(193, 225, 255);
  --text-color:black;
}

body{
    margin: 0;
    padding: 0;
    width: 100vw;
    height: 100vh;
    background-image: url('./assets/pattern1.jpg');
    background-repeat: no-repeat;
    background-size: cover;

}
h1,h2,h3,h4,h5,h6{
  
  font-family: 'Montserrat', sans-serif;

}
.App{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
   

}
.notif-div{
    width: 100%;
    height: 100px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: var(--primary-color);
    color: var(--tertiary-color);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 20px;
    padding: 10px;
}
.close-notif-btn{
  z-index: 2;
  
}
.top-bar{
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  background-color: var(--primary-color);
  
}
.top-bar h2{
  color: var(--tertiary-color);
}
.sort-div{
  position: absolute;
  top: 5px;
  right: 5px;
}
select{
  padding: 5px;
  background-color: transparent;
  border: 1px solid var(--tertiary-color);
  color: var(--tertiary-color);
}
select option{
  color: var(--text-color);
  padding: 5px;
}
.wrapper{
    width: 100%;
    height: calc(100% - 70px);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: scroll;
   
}
.wrapper::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.wrapper {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.btn-div{
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--primary-color);
    height: 70px;
}
table, td {
  /*border:1px solid black;
  border-collapse: collapse;
  box-shadow: 1px 1px 5px var(--primary-color);*/
  
  }
  th{
    border: 1px solid var(--tertiary-color);
    border-collapse: collapse;
  }
  table{
    width: 90%;
    margin: 20px 0px 70px 0;
  }
  th,td{
    padding: 20px;
    text-align: center;
  }
.btn,.close-notif-btn{
  background: linear-gradient(to right,var(--secondary-color),var(--tertiary-color));
  padding: 10px;
  border-radius: 5px;
  margin: 0 10px;
  width: 100px;
}
.btn:hover,.close-notif-btn:hover{
  background: linear-gradient(to right,var(--tertiary-color),var(--secondary-color) var(--primary-color));
  border: 1px solid var(--tertiary-color);
  color: var(--tertiary-color);
}
